<template>
	<view class="user-paypassword">
		<view class="inpt-wrap row verCenter"><input type="password" :value="newpsw" placeholder="新密码" placeholder-style="input-placeholder"
			 @input="inputchange1" /></view>
		<view class="inpt-wrap row verCenter"><input type="password" :value="againpsw" placeholder="确认密码" placeholder-class="input-placeholder"
			 @input="inputchange2" /></view>
		<view class="inpt-wrap row verCenter"><input type="text" :value="phone" placeholder="手机号码" placeholder-class="input-placeholder"
			 @input="inputchange3" /></view>
		<view class="inpt-wrap row verCenter bothSide">
			<input type="text" :value="smsCode" placeholder="请输入短信验证码" placeholder-class="input-placeholder" @input="inputchange4" />
			<view class="box">
				<button class="code" @click="send" v-if="!second">获取验证码</button>
				<view v-if="second"><text style="color:  #ec5951;">{{counter}}</text>秒后可重发</view>
			</view>
		</view>
		<view class="btn" @click="changepwd">修改密码</view>
	</view>
</template>

<script>
	import {
		API
	} from '@/util/api.js';
	import {
		myreg
	}from '@/util/util.js'
	export default {
		data() {
			return {
				newpsw: '',
				againpsw: '',
				phone: '',
				second: false,
				counter: 60,
				smsCode: ''

			};
		},
		methods: {
			inputchange1(e) {
				this.newpsw = e.target.value
			},
			inputchange2(e) {
				this.againpsw = e.target.value
			},
			inputchange3(e) {
				this.phone = e.target.value
			},
			inputchange4(e) {
				this.smsCode = e.target.value
			},
			changepwd() {
				var _ = this;
				if(_.newpsw!=_.againpsw){
					uni.showToast({
						title: '输入密码不一致，请重新输入',
						duration: 2000,
						});
					return
				};
				_.request(API.paySecret, 'post', {
					paySecret: _.newpsw,
					smsCode:_.smsCode
					
				}).then(res => {
					if (res.code === 10000) {
						uni.showToast({
							title: '修改成功',
							duration: 2000,
							success: function() {
								setTimeout(() => {
									uni.navigateBack()
								}, 2000)
							}
						});
					}
				})

			},
			send() {
				var _ = this;
				if(_.phone==""){
					uni.showToast({
						title: '请输入手机号码',
						duration: 2000,
						});
						return
				}else{
					if(myreg.test(this.phone)==false){
						uni.showToast({
							title: '号码不正确',
							duration: 2000,
							});
							return
					}
				};
				_.request(API.sendSms, 'get', {
					phone: _.phone,
					type: 'checkAuth'
				}).then(res => {
					if (res.code === 10000) {
						console.log(res);
						_.second = true;
						if (_.second) {
							if (_.counter > 0) {
								var timer = setInterval(() => {
									_.counter--;
								}, 1000)
							} else {
								clearInterval(timer);
								_.counter = 60;
								_.second = false
							}
						}
					} else {
						console.log(res)
					}
				})

			}

		}
	};
</script>

<style lang="scss">
	@import '../../assets/css/user/payPassword.scss';
</style>
